export const CHANGE_GROSS_INCOME = 'tax/CHANGE_GROSS_INCOME'
export const CHANGE_NON_TAXABLE = 'tax/CHANGE_NON_TAXABLE'
export const CHANGE_PAID_PCB_SUM = 'tax/CHANGE_PAID_PCB_SUM'

const initialState = {
  eligibleRelief: 0,
  grossIncome: '',
  nonTaxable: '',
  paidPcbSum: 0,
  taxFormula: taxable => {
    var taxAmt = 0
    if (taxable >= 1000000) {
      taxAmt = 238650 + (taxable - 1000000) * 0.28
    }
    else if (taxable >= 600000) {
      taxAmt = 134650 + (taxable - 600000) * 0.26
    }
    else if (taxable >= 400000) {
      taxAmt = 84650 + (taxable - 400000) * 0.25
    }
    else if (taxable >= 250000) {
      taxAmt = 47900 + (taxable - 250000) * 0.245
    }
    else if (taxable >= 100000) {
      taxAmt = 11900 + (taxable - 100000) * 0.24
    }
    else if (taxable >= 70000) {
      taxAmt = 5600 + (taxable - 70000) * 0.21
    }
    else if (taxable >= 50000) {
      taxAmt = 2400 + (taxable - 50000) * 0.16
    }
    else if (taxable >= 35000) {
      taxAmt = 900 + (taxable - 35000) * 0.1
    }
    else if (taxable >= 20000) {
      taxAmt = 150 + (taxable - 20000) * 0.05
    }
    else if (taxable >= 5000) {
      taxAmt = (taxable - 5000) * 0.01
    }
    return taxAmt
  }
}

export default (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_GROSS_INCOME:
      return {
        ...state,
        grossIncome: action.value
      }
    case CHANGE_NON_TAXABLE:
      return {
        ...state,
        nonTaxable: action.value
      }

    case CHANGE_PAID_PCB_SUM:
      return {
        ...state,
        paidPcbSum: action.value
      }

    default:
      return state
  }
}

export const changeGrossIncome = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_GROSS_INCOME,
      value: value
    })
  }
}

export const changeNonTaxable = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_NON_TAXABLE,
      value: value
    })
  }
}

export const changeTaxable = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_NON_TAXABLE,
      value: value
    })
  }
}

export const changePaidPcbSum = value => {
  return dispatch => {
    dispatch({
      type: CHANGE_PAID_PCB_SUM,
      value: value
    })
  }
}




