export const NAVIGATE_NEXT = 'calculator/NAVIGATE_NEXT'
export const NAVIGATE_BACK = 'calculator/NAVIGATE_BACK'
export const TOGGLE_MODAL = 'calculator/TOGGLE_MODAL'



const initialState = {
  view: 'reliefs',
  inputRegex: /^[\d]*[\\.]?[\d]*$/,
  modal: false,
  modalText: ['']
}


export default (state = initialState, action) => {
  switch (action.type) {
    case NAVIGATE_NEXT:
      if (state.view === 'reliefs') {
        return {
          ...state,
          view: 'result'
        }
      }
      return state

    case NAVIGATE_BACK:
      if (state.view === 'result') {
        return {
          ...state,
          view: 'reliefs'
        }
      }
      return state

    case TOGGLE_MODAL:
      return {
        ...state,
        modal: !state.modal,
        modalText: action.remarks
      }

    default:
      return state
  }
}

export const navigateNext = () => {
  return dispatch => {
    dispatch({
      type: NAVIGATE_NEXT
    })
  }
}

export const navigateBack = () => {
  return dispatch => {
    dispatch({
      type: NAVIGATE_BACK
    })
  }
}


export const toggleModal = (remarks) => {
  return dispatch => {
    dispatch({
      type: TOGGLE_MODAL,
      remarks: remarks
    })
  }
}



