export const CHANGE_TAB = 'nav/CHANGE_TAB'
export const TOGGLE_NAV = 'nav/TOGGLE_NAV'



const initialState = {
  tab: 'calculator',
  isNavOpen: false
}

export default (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_TAB:
      return {
        ...state,
        tab : action.newTab
      }
    case TOGGLE_NAV:
      return {
        ...state,
        isNavOpen: !state.isNavOpen
      }
    default:
      return state
  }
}

export const changeTab = (newTab) => {
  return dispatch => {
    dispatch({
      type: CHANGE_TAB,
      newTab: newTab
    })
  }
}

export const toggleNav = () => {
  return dispatch => {
    dispatch(
      {
        type: TOGGLE_NAV
      }
    )
  }
}



