import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Alert } from 'reactstrap'

const FloatingAlert = props =>
  (
    <div>
      {props.errorIds.length > 0 &&
        <Alert color='danger' className='rounded position-sticky' style={{ 'top': '85px', 'zIndex': '10' }}>
          <h5 className='alert-heading'>Validation Errors</h5>
          <hr />
          <p className='m-0'>
            <span>Please re-enter amount for row# </span>
            {
              props.errorIds.map(
                (id, index) =>
                  <span  key={props.errorIds}>
                    <span className='font-weight-bold'>{id}</span>
                    <span>{index < props.errorIds.length - 1 && ', '}</span>
                  </span>
              )
            }
          </p>
        </Alert>}
    </div>
  )


const mapStateToProps = state => ({
  errorIds: state.reliefs.errorIds
})


const mapDispatchToProps = dispatch => bindActionCreators({
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FloatingAlert)



