import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Row, Col, Input, Button } from 'reactstrap'
import { addError, removeError, changeValue } from '../../modules/reliefs'
import { toggleModal } from '../../modules/calculator'

const Reliefs = props => (
  <div className='pt-2'>
    {props.info.map(item =>
      <Row key={item.id} noGutters>
        <Col xs='1'>
          <p className='p-1'>{item.id}</p>
        </Col>
        <Col xs='6' sm='7' className='p-1 pt-1 pb-2'>
          <span>{item.description}</span>
          {
            item.remarks &&
            <span>.. <button onClick={props.toggleModal.bind(this, item.remarks)} className='my-info px-2 py-3'>
            </button></span>
          }
        </Col>
        <Col xs='2'>
          <p className='p-1 text-left'>{item.limit}</p>
        </Col>
        <Col xs='3' sm='2'>
          <p className='p-1'>
            <Input placeholder='Enter Amount'
              className='text-truncate'
              type='text' pattern='\d*'
              valid={props.errorIds.indexOf(item.id) > -1 ? false : item.value ? true : null}
              value={item.value}
              onChange={
                event => {
                  var newVal = event.target.value
                  if (!props.inputRegex.test(newVal)) {
                    newVal = ''
                  }

                  var decimals = (newVal + '').split(".")[1]
                  if (decimals && +decimals > 99) {
                    newVal = Math.floor(newVal * 100) / 100
                  }

                  props.changeValue(item.id, newVal)

                  if (newVal < 0 || newVal > item.limit) {
                    props.addError(item.id)
                  } else {
                    props.removeError(item.id)
                  }
                }
              } />
          </p>
        </Col>
      </Row>)
    }
  </div>
)




const mapStateToProps = state => ({
  info: state.reliefs.info,
  errorIds: state.reliefs.errorIds,
  eligibleRelief: state.reliefs.eligibleRelief,
  inputRegex: state.calculator.inputRegex
})


const mapDispatchToProps = dispatch => bindActionCreators({
  addError,
  removeError,
  changeValue,
  toggleModal
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Reliefs)



