import React from 'react';
import { PropTypes } from 'prop-types'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';
import { NavLink as RRNavLink, withRouter } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { changeTab, toggleNav } from '../../modules/nav'

const AppNavBar = props => (
  <div>
    <div style={{ 'height': '70px' }}></div>

    <div className='fixed-top' >
      <Navbar color='light' light expand='md' style={{ 'backgroundColor': '#F8F8F8' }}>
        <NavbarBrand href='/'>
          <span class='h4'>Tax Refund Calculator 2017 </span>
          <button class='my-money py-3 px-2'></button>
          <button class='my-winged-money py-3 px-2'></button>
        </NavbarBrand>
        <NavbarToggler onClick={props.toggleNav} />
        <Collapse isOpen={props.isNavOpen} navbar>
          <Nav className='ml-auto' pills>
            <NavItem>
              <NavLink activeClassName='bg-info text-white font-weight-bold' className='font-weight-bold'
                exact tag={RRNavLink} to='/'>Calculator</NavLink>
            </NavItem>
            <NavItem>
              <NavLink activeClassName='bg-info text-white font-weight-bold' className='font-weight-bold'
                exact tag={RRNavLink} to='/rate'>Tax Rates</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  </div>

)


AppNavBar.propTypes = {
  tab: PropTypes.string.isRequired,
  isNavOpen: PropTypes.bool.isRequired
}


const mapStateToProps = state => ({
  tab: state.nav.tab,
  isNavOpen: state.nav.isNavOpen
})

const mapDispatchToProps = dispatch => bindActionCreators({
  changeTab,
  toggleNav
}, dispatch)

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(AppNavBar))



