import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'
import Reliefs from '../reliefs'
import FloatingAlert from '../floating-alert'
import ReliefsHeader from '../reliefs-header'
import { navigateNext, navigateBack, toggleModal } from '../../modules/calculator'
import TaxInfo from '../tax-info'

const Home = props =>
  (
    <div className='bg-white m-1 mt-3'>
      <Modal isOpen={props.modal} toggle={props.toggleModal}>
        <ModalHeader toggle={props.toggleModal.bind(this, [''])} >
          <p>Remarks</p>
        </ModalHeader>
        <ModalBody>
          {props.modalText && props.modalText.map(item => <p className='p-1 my-1' key={item}>{item}</p>)}
        </ModalBody>
        <ModalFooter className='d-flex flex-column'>
          <Button className='justify-content-center' color='primary' onClick={props.toggleModal.bind(this, [''])}>Close</Button>
        </ModalFooter>
      </Modal>

      <FloatingAlert />
      <div style={{ 'display': 'flex', 'flexDirection': 'column' }}>
        {
          props.view === 'reliefs' &&
          <div>
            <ReliefsHeader />
            <Reliefs />
          </div>
        }
        {
          props.view === 'result' &&
          <div>
            <TaxInfo />
          </div>
        }
        <div className='my-4'></div>
        {
          props.view === 'reliefs' &&
          <Button color='primary' className='floating-btn'
            onClick={props.navigateNext} disabled={props.errorIds.length > 0}>Next</Button>
        }
        {
          props.view === 'result' &&
          <Button color='primary' className='floating-btn'
            onClick={props.navigateBack}>Back</Button>
        }
      </div>
    </div >

  )





const mapStateToProps = state => ({
  view: state.calculator.view,
  errorIds: state.reliefs.errorIds,
  modal: state.calculator.modal,
  modalText: state.calculator.modalText
})


const mapDispatchToProps = dispatch => bindActionCreators({
  navigateNext,
  navigateBack,
  toggleModal
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)



