import React from 'react'
import { Row, Col, Input } from 'reactstrap'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { changeGrossIncome, changeNonTaxable, changePaidPcbSum } from '../../modules/tax-info'



const TaxInfo = props => (
  <div>
    <div className='m-2'></div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h5>Eligible Tax Relief</h5></Col>
        <Col className='col-lg-2'>
          <div
            className='text-center bg-light form-control font-weight-bold'>
            {props.eligibleRelief.toFixed(2)}
          </div>
        </Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h5>Gross Income</h5></Col>
        <Col className='col-lg-2'>
          <Input placeholder='Enter Amt' type='text' pattern='\d*'
            value={props.grossIncome}
            onChange={
              event => {
                var newVal = event.target.value
                if (!props.inputRegex.test(newVal)) {
                  newVal = ''
                }

                var decimals = (newVal + '').split(".")[1]
                if (decimals && +decimals > 99) {
                  newVal = Math.floor(newVal * 100) / 100
                }

                props.changeGrossIncome(newVal)
              }
            } />
        </Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h5>Non-taxable allowance</h5></Col>
        <Col className='col-lg-2'><Input
          placeholder='Enter Amt'
          type='text' pattern='\d*'
          value={props.nonTaxable}
          onChange={
            event => {
              var newVal = event.target.value
              if (!props.inputRegex.test(newVal)) {
                newVal = ''
              }

              var decimals = (newVal + '').split(".")[1]
              if (decimals && +decimals > 99) {
                newVal = Math.floor(newVal * 100) / 100
              }

              props.changeNonTaxable(newVal)
            }
          } /></Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'>
          <span className='h5'>Taxable Income</span>
          <span className='h5 float-right font-weight-bold'>=</span>
        </Col>
        <Col className='col-lg-2'>
          <div
            className='text-center bg-success text-white form-control font-weight-bold'>
            {props.grossIncome - props.nonTaxable - props.eligibleRelief > 0
              ? (props.grossIncome - props.nonTaxable - props.eligibleRelief).toFixed(2)
              : '-'}
          </div>
        </Col>
      </Row>
    </div>
    <div className='my-5'></div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h5>Tax Payable Amount</h5></Col>
        <Col className='col-lg-2'>
          <div
            className='text-center bg-light form-control font-weight-bold'>
            {props.grossIncome - props.nonTaxable - props.eligibleRelief <= 0 ? '-' :
              +props.taxFormula(props.grossIncome - props.nonTaxable - props.eligibleRelief) <= 0 ? '-'
                : props.taxFormula(props.grossIncome - props.nonTaxable - props.eligibleRelief).toFixed(2)}
          </div>
        </Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'><h5>Annual PCB Sum</h5></Col>
        <Col className='col-lg-2'><Input
          placeholder='Enter Amt'
          type='text' pattern='\d*'
          value={props.paidPcbSum}
          onChange={
            event => {
              var newVal = event.target.value
              if (!props.inputRegex.test(newVal)) {
                newVal = ''
              }

              var decimals = (newVal + '').split(".")[1]
              if (decimals && +decimals > 99) {
                newVal = Math.floor(newVal * 100) / 100
              }

              props.changePaidPcbSum(newVal)
            }
          } /></Col>
      </Row>
    </div>
    <div className='my-2'>
      <Row className='justify-content-md-center'>
        <Col className='col-lg-4'>
          <span className='h5'>Eligible Tax Return</span>
          <span className='h5 float-right font-weight-bold'>=</span>
        </Col>
        <Col className='col-lg-2'>
          <div
            className='text-center bg-success text-white form-control font-weight-bold'>
            {props.grossIncome - props.nonTaxable - props.eligibleRelief <= 0 ? '-' :
              (props.paidPcbSum - +props.taxFormula(props.grossIncome - props.nonTaxable - props.eligibleRelief)) <= 0 ? '-'
                : (props.paidPcbSum - +props.taxFormula(props.grossIncome - props.nonTaxable - props.eligibleRelief)).toFixed(2)}
          </div>
        </Col>
      </Row>
    </div>
  </div>
)




const mapStateToProps = state => ({
  eligibleRelief: state.reliefs.eligibleRelief,
  grossIncome: state.taxInfo.grossIncome,
  nonTaxable: state.taxInfo.nonTaxable,
  paidPcbSum: state.taxInfo.paidPcbSum,
  inputRegex: state.calculator.inputRegex,
  taxFormula: state.taxInfo.taxFormula
})



const mapDispatchToProps = dispatch => bindActionCreators({
  changeGrossIncome,
  changeNonTaxable,
  changePaidPcbSum
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TaxInfo)



